import classes from "./Cart.module.css";
import Modal from "../UI/Modal";
import CartItem from "./CartItem";
import CartContext from "../../store/cart-context";
import React, { useContext, useState } from "react";
import Checkout from "./Checkout";
const Cart = (props) => {
  const cartCtx = useContext(CartContext);
  const [isCheckout, setIsCheckout] = useState(false);
  const [isSubmiting, setIsSubmiting] = useState(false);
  const [didSubmit, setDidSubmit] = useState(false);
  const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`;
  const hasItems = cartCtx.items.length > 0;
  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id);
  };

  const cartItemAddHandler = (item) => {
    cartCtx.addItem(item);
  };

  const cartItems = (
    <ul className={classes["cart-items"]}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          price={item.price}
          amount={item.amount}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
          name={item.name}
        />
      ))}
    </ul>
  );
  const checkoutOnClick = () => {
    setIsCheckout(true);
  };
  const submitOrderHandler = async (userData) => {
    try {
      setIsSubmiting(true);
      const response = await fetch(
        "https://react-http-9bce5-default-rtdb.firebaseio.com/orders.json",
        {
          method: "POST",
          body: JSON.stringify({
            user: userData,
            orderedItems: cartCtx.items,
          }),
        }
      );
      console.log(response);
      if (!response.ok) {
        throw new Error("Something went wrong");
      }

      setIsSubmiting(false);
      setDidSubmit(true);
      cartCtx.clear();
    } catch (e) {
      console.log(e);
    }
  };
  const buttonActions = (
    <React.Fragment>
      <div className={classes.actions}>
        <button className={classes["button--alt"]} onClick={props.onClose}>
          Close
        </button>

        {hasItems && (
          <button className={classes.button} onClick={checkoutOnClick}>
            Order
          </button>
        )}
      </div>
    </React.Fragment>
  );
  const modalContent = (
    <React.Fragment>
      {cartItems}
      <div className={classes.total}>
        <span>Total Amount</span>
        <div>{totalAmount}</div>
      </div>

      {isCheckout && (
        <Checkout onCancel={props.onClose} onConfirm={submitOrderHandler} />
      )}
      {!isCheckout && buttonActions}
    </React.Fragment>
  );
  const loading = <p>Please, await a few seconds</p>;
  const submitted = (
    <React.Fragment>
      <p>Congrats, we received your order!</p>
      <div className={classes.actions}>
        <button onClick={props.onClose}>Close</button>
      </div>
    </React.Fragment>
  );
  return (
    <Modal onClose={props.onClose}>
      {!isSubmiting && !didSubmit && modalContent}
      {isSubmiting && loading}
      {didSubmit && !isSubmiting && submitted}
    </Modal>
  );
};

export default Cart;
