import classes from "./Checkout.module.css";
import { useRef, useState } from "react";
const isEmpty = (value) => value.trim() === "";
const isFiveChars = (value) => value.trim().length === 5;
const Checkout = (props) => {
  const [formValidity, setFormValidity] = useState({
    name: true,
    street: true,
    city: true,
    postal: true,
  });

  const nameRef = useRef();
  const streetRef = useRef();
  const postalRef = useRef();
  const cityRef = useRef();

  const onSubmitFunction = (e) => {
    e.preventDefault();
    const nameValue = nameRef.current.value;
    const streetValue = streetRef.current.value;
    const postalValue = postalRef.current.value;
    const cityValue = cityRef.current.value;

    const nameValid = !isEmpty(nameValue);
    const streetValid = !isEmpty(streetValue);
    const postalValid = isFiveChars(postalValue);
    const cityValid = !isEmpty(cityValue);
    setFormValidity({
      name: nameValid,
      street: streetValid,
      postal: postalValid,
      city: cityValid,
    });
    const formIsValid = nameValid && streetValid && postalValid && cityValid;
    console.log(formIsValid);
    if (!formIsValid) {
      return;
    }

    props.onConfirm({
      name: nameValue,
      street: streetValue,
      postal: postalValue,
      city: cityValue,
    });
  };
  const inputControlClasses = (value) => {
    return `${classes.control}  ${value ? "" : classes.invalid} `;
  };
  return (
    <form className={classes.form} onSubmit={onSubmitFunction}>
      <div className={inputControlClasses(formValidity.name)}>
        <label htmlFor="name">Your Name</label>
        <input type="text" id="name" ref={nameRef} />
        {!formValidity.name && <p>Please, enter a name</p>}
      </div>
      <div className={inputControlClasses(formValidity.street)}>
        <label htmlFor="street">Street</label>
        <input type="text" id="street" ref={streetRef} />
        {!formValidity.name && <p>Please, enter a street</p>}
      </div>
      <div className={inputControlClasses(formValidity.postal)}>
        <label htmlFor="postal">Postal Code</label>
        <input type="text" id="postal" ref={postalRef} />
        {!formValidity.name && <p>Please, enter a postal code(5 numbers)</p>}
      </div>
      <div className={inputControlClasses(formValidity.city)}>
        <label htmlFor="city">City</label>
        <input type="text" id="city" ref={cityRef} />
        {!formValidity.name && <p>Please, enter a city</p>}
      </div>
      <div className={classes.actions}>
        <button type="button" onClick={props.onCancel}>
          Cancel
        </button>
        <button className={classes.submit}>OK</button>
      </div>
    </form>
  );
};

export default Checkout;
