import classes from "./AvailableMeals.module.css";
import Card from "../UI/Card";
import MealItem from "./MealItem/MealItem";
import { useEffect, useState, useReducer } from "react";

const initialStateItems = {
  message: "",
};
const stateItemsReducer = (state, action) => {
  if (action.type === "ISLOADING") {
    return {
      message: "Loading, please await...",
    };
  }
  if (action.type === "ERROR") {
    console.log(action);
    return {
      message: action.message,
    };
  }
  if (action.type === "DONE") {
    return {
      message: "",
    };
  }
  return initialStateItems;
};
const AvailableMeals = (props) => {
  const [meals, setMeals] = useState([]);
  const [stateItems, dispatch] = useReducer(
    stateItemsReducer,
    initialStateItems
  );
  useEffect(() => {
    const getMeals = async () => {
      dispatch({ type: "ISLOADING" });
      const response = await fetch(
        "https://react-http-9bce5-default-rtdb.firebaseio.com/Meals.json"
      );

      if (!response.ok) {
        throw new Error("Something is wrong");
        console.log("asdadasd");
      }
      const data = await response.json();
      let theMeals = [];
      for (const key in data) {
        theMeals.push({
          id: key,
          name: data[key].name,
          price: data[key].price,
          description: data[key].description,
        });
      }

      setMeals(theMeals);
      dispatch({ type: "DONE" });
    };

    getMeals().catch((e) => {
      dispatch({
        type: "ERROR",
        message: "Something went wrong: " + e.message,
      });
    });
  }, []);
  if (stateItems.message !== "") {
    return <p className={classes.message}>{stateItems.message}</p>;
  }
  const mealsList = meals.map((item) => (
    <MealItem
      id={item.id}
      key={item.id}
      name={item.name}
      description={item.description}
      price={item.price}
    />
  ));

  return (
    <section className={classes.meals}>
      <Card>
        <ul>{mealsList}</ul>
      </Card>
    </section>
  );
};

export default AvailableMeals;
